import {GetSchoolInfo} from '@/api/school'
export default {
	props:{
		schoolid:Number,
		show:false
	},
	data(){
		return{
			courseList:[],//新闻列表
			courseListHot:[],
			getParameters:{
				schoolid:this.schoolid
			},
			refreshStatus:false,
			height:0,
			isshowprice:false
		}
	},
	mounted(){
		uni.showLoading({
		    title: '加载中'
		});
		uni.getSystemInfo({
			success:res=>{
				this.height = res.windowHeight-226
			}
		})
		this.loadPage()
	},
	methods: {
		onRefresh(){
			this.loadPage()
		},
		open(item){
			var url = encodeURIComponent(item.weburl)
			uni.navigateTo({
				url:'/pages/web/web?title='+item.name+'&photo='+item.photo+'&url='+url
			})
			
		},
		//初始数据的请求
		loadPage(){
			this.isshowprice = this.$store.state.isshowprice
			if(this.loading){
				return
			}
			this.loading = true
			GetSchoolInfo(this.getParameters).then(res=>{
				if(res.result){
					this.courseList = res.courseList
					this.courseListHot = res.courseListHot
				}else{
					this.courseList = []
					this.courseListHot = []
				}
				uni.hideLoading();
				this.loading = false
				setTimeout(()=>{
					this.refreshStatus = false
				},1000)
			})
		}
		
	}
}
